$background_color: #5d5fef;
$lighter_shade: #ffffff;
$background_shade: #fafbfc;
.page {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: $background_shade;
  position: relative;
  overflow-x: hidden;
  font-size: 1vw;
  
  .list {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;

    .inner_list {
      // padding-top: 50px;
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      flex-direction: column;

      .filter_button {
        height: 45px;
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0px 15px;
        gap: 8px;
        // border: 2px solid red;
        position: relative;
        // border: 2px solid red;
        margin-top: 4px;

        .page-filter-add{
          display: flex;
          gap: 10px;
         
        }
        .page-header-content{
          width:100%;
          display: flex;
          justify-content: space-between;
        }

        .filter-popup {
          position: absolute;
          top: 40px;
          display: flex;
          width: fit-content;
          align-items: center;
          justify-content: center;
          // border: 2px solid red;
          padding: 15px;
          border-radius: 5px;
          flex-direction: column;
          background-color: white;
          // box-shadow: #5d5fef 0px 0px 3px 0px,
          //   #5d5fef 0px 0px 0px 0px;
          box-shadow: #e5ebef 0px 0px 3px 2px;
          gap: 25px;
          .filter-options {
            width: fit-content;
            display: flex;
            justify-content: center;
            align-items: center;
            gap: 20px;

            .dropdown {
              // border: 2px solid red;
              width: fit-content;
              // background-color: $background_shade;
              display: flex;
              justify-content: center;
              align-items: center;
              flex-direction: column;
              gap: 5px;
              select {
                color: #737791;
                cursor: pointer;
                font-size: 0.9vw;
                outline: none;
                // border: 0.5px solid #737791;
                padding: 6px 12px;
                border-radius: 3px;
                // border: none;
                box-shadow: #e5ebef 0px 0px 0px 1px;
                border: none;
              }
              p {
                width: 100%;
                border: 1px solid #e5ebef;

                border-radius: 5px;
                padding: 6px;
                display: flex;
                justify-content: space-between;
                align-items: center;
                gap: 10px;
                background-color: $background_shade;
                font-size: 0.7rem;
                color: #737791;
              }
            }
          }

          .apply-button-api {
            width: 100%;
            display: flex;
            justify-content: flex-end;
            align-items: center;
            gap: 10px;
            .reset {
              height: fit-content;
              width: fit-content;
              border-radius: 3px;

              box-shadow: rgba(0, 0, 0, 0.02) 0px 1px 3px 0px,
                rgba(27, 31, 35, 0.15) 0px 0px 0px 1px;
              button {
                // height: 100%;
                padding: 6px 15px;
                cursor: pointer;
                border: none;
                border-radius: 3px;
                font-size: 1vw;
                background-color: $lighter_shade;
                color: #737791;
                font-weight: 500;
              }
              button:hover {
                background-color: $background_color;
                color: $background_shade;
              }
            }

            .apply_filter {
              height: fit-content;
              width: fit-content;
              border-radius: 3px;
              box-shadow: rgba(67, 71, 85, 0.27) 0px 0px 0.25em,
                rgba(90, 125, 188, 0.05) 0px 0.25em 1em;
              button {
                padding: 6px 15px;
                cursor: pointer;
                border: none;
                background-color: $lighter_shade;
                color: #5d5fef;

                border-radius: 3px;
                font-size: 1vw;
              }
              button:hover {
                background-color: $background_color;
                color: $background_shade;
              }
            }
          }
        }

        .filter-icon {
          height: 100%;
          display: flex;
          justify-content: center;
          align-items: flex-start;
          span {
            display: flex;
            justify-content: center;
            align-items: flex-start;
            font-size: 1.5rem;
            color: #737791;
          }
        }

        // .dropdown{
        //   // border: 2px solid red;
        //   width: fit-content;
        //   background-color: $background_shade;
        //   select{
        //     color: #737791;
        //     cursor: pointer;
