        .edit_button {
          height: fit-content;
          width: fit-content;
          border-radius: 3px;
          box-shadow: rgba(67, 71, 85, 0.27) 0px 0px 0.25em,
            rgba(90, 125, 188, 0.05) 0px 0.25em 1em;
          button {
            padding: 8px 15px;
            cursor: pointer;
            border: none;
            background-color: $lighter_shade;
            color: #737791;

            border-radius: 3px;
            font-size: 1vw;
          }
        }

        .upload_button {
          height: fit-content;
          width: fit-content;
          // box-shadow: rgba(67, 71, 85, 0.27) 0px 0px 0.25em,
          //   rgba(90, 125, 188, 0.05) 0px 0.25em 1em;
          border-radius: 3px;
          box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
          background-color: $lighter_shade;
        }
        .upload_report-button {
          height: fit-content;
          width: fit-content;
          box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
          // box-shadow: rgba(67, 71, 85, 0.27) 0px 0px 0.25em,
          //   rgba(90, 125, 188, 0.05) 0px 0.25em 1em;
          border-radius: 3px;
          background-color: $lighter_shade;
        }
        .report {
          height: fit-content;
          width: fit-content;
          // box-shadow: rgba(67, 71, 85, 0.27) 0px 0px 0.25em,
          //   rgba(90, 125, 188, 0.05) 0px 0.25em 1em;
          box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
          border-radius: 3px;
          cursor: pointer;

          button {
            border-radius: 10px;
            padding: 3px;
            border: none;
            background-color: $lighter_shade;
            color: #737791;

            #analytic {
              font-size: 1.3rem;
            }
          }
        }
      }
      .table {
        width: 100%;
        height: calc(90% - 45px);
        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-direction: column;
        gap: 2px;
        padding: 7px 15px;
        overflow: hidden;
        // border: 2px solid red;
        
        .no-data{
            
            width: 100%;
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 1.5vw;
            color: #737791;
        }

        // table {
