@media screen and (max-width: 2559px) and (min-width: 1920px) {
  .page {
    .list {
      .inner_list {
        // padding-top: 80px;
        .filter_button {
          height: 70px;
        }
        .table {
          height: calc(100% - 70px);

          table {
            width: 100%;

            thead {
              height: 70px;
              tr {
                th {
                  padding: 1.8vh 1vw;
                }
              }
            }

            tbody {
              width: 100%;
              tr {
                height: 50px;
                td {
                  padding: 1.8vh 1vw;
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (min-width: 2560px) {
  .page {
    .list {
      .inner_list {
        // padding-top: 80px;
        .filter_button {
          height: 90px;
        }
        .table {
          height: calc(100% - 00px);

          table {
            width: 100%;

            thead {
              height: 120px;
              tr {
                th {
                  padding: 1.8vh 1vw;
                }
              }
            }

            tbody {
              width: 100%;
              tr {
                height: 103px;
                td {
                  padding: 1.8vh 1vw;
                }
              }
            }
          }
        }
      }
    }
  }
}
