@import "styles/globals/variables";
.table-bordered {
  width: 100%;
  height: fit-content;
  border-collapse: collapse;
  overflow-y: auto;
  overflow-x: hidden;
  position: relative;
  box-shadow:
      #dfe5f9 0px 1px 3px 0px,
      #dfe5f9 0px 0px 0px 1px;
  table {
    border-collapse: collapse;
    max-height: 100%;
    width: 100%;
    // box-shadow:
    //   #dfe5f9 0px 1px 3px 0px,
    //   #dfe5f9 0px 0px 0px 1px;

    thead {
      width: 100%;
      background-color: #f9f9f9;
      position: sticky;
      top: 0px;
      z-index: 1;
      
    }
    thead {
      tr{
        th{
          padding: 10px;
        }
      }
    }
    tbody {
      tr{
        // max-height: 80px;
        // border: 2px solid red;
      }
    }
  }

  th,
  td {
    border: 1px solid #e8e8e8;
    padding: 8px;
    text-align: left;
    max-height: 60px;
  }
  th {
    font-size: 0.85vw;
    font-weight: 500;
    color: #121c2d;
    padding: 15px;
  }
}

.table-bordered::-webkit-scrollbar {
  width: 5px;

}

/* Track */
.table-bordered::-webkit-scrollbar-track {
  background: white;
}

/* Handle */
.table-bordered::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 50px;
}

/* Handle on hover */
.table-bordered::-webkit-scrollbar-thumb:hover {
  background: #555;
}
