

.dropdown-button{
	// border: 2px solid red;
	position: relative;
	&:hover {
		cursor: pointer;
	}
}
	.dropdown-container {
		background-color: #fff;
		border-radius: 10px;
		transition: all 0.2s linear;
		padding: 8px;
		position: absolute;
		right: 0px;
		top: 40px;
		box-shadow: 5px 12px 15px rgba(0, 0, 1, 0.1);
		display: flex;
		flex-direction: column;
		gap: 10px;
		// border: 2px solid red
		z-index: 10;
		
	}

	.selection-container {
		display: flex;
		flex-direction: row;
		border-bottom-width: 1px;
		cursor: pointer;
		border-color: black;
		gap:10px;
		padding: 5px;
		font-size: 14px;
	.text-spacing{
		white-space: nowrap; 
		overflow: hidden;    
		text-overflow: ellipsis;
	}
		&:hover {
			background-color: rgba(0, 0, 0, 0.04);
			border-radius: 5px;
		}
	}
