.accordian {
  width: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  // background-color: #ebeef2;
  gap: 5px;
  transition: all 0.3s ease-in-out;
  padding: 5px 5px;
  border-radius: 4px;
  border: 1.5px solid #ebeef2;
  margin-bottom: 10px;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
  // background-color: #f1f2f4;

  .accordian-info {
    width: 100%;
/*     height: 30px; */ /* Keep a fixed height */
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 5px;
    .name {
      width: 55%;
      padding: 0px 3px;
      overflow-wrap: break-word;     
      word-wrap: break-word;
      hyphens: auto;         
    }
.natco-name{
  width:10%;
}
    p {
      font-size: 1vw;
      font-weight: 500;
      color: #455264;
    }

    a {
      text-decoration: none;
      color: #455264;
    }
    .expand {
      cursor: pointer;
    }
  }

  .accordian-content {
    width: 100%;
    height: 250px;
    overflow: hidden;
    // border: 2px solid red;
    transition: height 1s ease-in-out;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 30px;
    background-color: white;
    padding: 0px 10px;
  
    .accordian-script-info {
      display: flex;
      justify-content: center;
      align-items: flex-start;
      width: 100%;
      height: 100%;
      border: 1.5px solid #ebeef2;
      border-radius: 4px;

      .left-content-a {
        width: 70%;
        height: 100%;
        // border-right: 1.5px solid #ebeef2;
        .desc-a {
          width: 100%;
          height: 100%;
          // border: 2px solid red;
          display: flex;
          justify-content: flex-start;
          align-items: flex-start;
          gap: 10px;
          flex-direction: column;
          padding: 10px;

          p {
            font-size: 0.9vw;
            color: #455264;
          }
        }
      }

      .right-content {
        width: 30%;
        height: 100%;
        padding: 10px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-direction: column;
        gap: 10px;
        border-left:  1.5px solid #ebeef2;;
        .people {
          width: 100%;
          display: flex;
          justify-content: center;
          align-items: center;
          flex-direction: column;
          gap: 10px;

          .name {
            width: 100%;
            font-size: 1vw;
            font-weight: 500;
            color: #455264;
            display: flex;
            justify-content: space-between;
            align-items: center;
            // border: 2px solid red;
          }

          .role-section {
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            gap: 10px;
            flex-direction: column;

            .stp {
              width: 100%;
              display: flex;
              justify-content: flex-start;
              align-items: center;
              // flex-direction: column;
              // border: 2px solid red;

              p {
              }
            }
          }
        }
      }
    }
    .accordian-script-issue {
      width: 100%;
    }
  }
}

.open-a {
  // border: 1.5px solid #04548c;
  box-shadow: 0 1px 2px 0 #04548c;
  .accordian-content {
    height: fit-content; /* Expanded height */
    // border: 1.5px solid #ebeef2;
    // box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
  
  }
}

.close-a {
  .accordian-content {
    height: 0; /* Collapsed height */
    overflow: hidden;
  }
}
