@import "styles/globals/variables";

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px 12px;
  border: 0;
  cursor: pointer;
  border-radius: 5px;

  .fullWidth {
    width: 100%;
  }

  border-radius: 3px;
  // box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
  .icon-left {
    margin-right: 3px;
  }

  .icon-right {
    margin-left: 3px;
  }

  // Button type styles
  &.primary {
    background-color: $color-primary;
    color: $color-white;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
    &:hover {
    }
  }

  &.plain {
    background-color: $color-white;
  }


  &.border {
    border: 1px solid $color-primary;
    background-color: white;
  }

  &.secondary {
    background-color: $color-white;
    color: $color-grey-600;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
    &:hover {
    }
  }

  // Variants
  &.outlined {
    background-color: transparent;
    border: 1px solid $color-primary;
    color: $color-primary;
  }
  &.small {
    font-size: 0.8vw;
    padding: 4px 8px;
  }

  &.medium {
    font-size: 14px;
    padding: 8px 12px;
  }

  &.large {
    font-size: 16px;
    padding: 12px 16px;
  }
  &.text{
    background-color: transparent;
    color:#cac3d9;

    &:active{
      background-color: #475a74;
    }
  }

  // disabled
  &.button-disabled{
    cursor: not-allowed;
    opacity: 0.6;
    color: $color-grey-600;
  }
}
