.search-bar {
    display: flex;
    align-items: center;
    border: 1px solid #ccc;
    border-radius: 8px;
    padding: 4px;
    background-color: #fff;
  }

  .input-wrapper {
    position: relative;
    display: inline-block;
  }

  .input-wrapper input {
    padding-right: 2rem; 
  }
  
  .clear-button {
    position: absolute;
    right: 0.5rem;
    top: 50%;
    transform: translateY(-50%);
    background: none;
    border: none;
    font-size: 1rem;
    cursor: pointer;
    color: #999;
  }
  
   .search-bar input, .search-bar select{
    border: none;
    outline: none;
    padding: 6px;
    font-size: 12px;
    border-radius: 6px 0 0 6px;
  }
  
  .search-bar button {
    background: none;
    border: none;
    cursor: pointer;
    padding: 6px;
    border-radius: 0 6px 6px 0;
  }
  
  .search-bar button:hover {
    background-color: #f0f0f0;
  }
  